@import url(https://fonts.googleapis.com/css2?family=Francois+One&family=Roboto:wght@700&display=swap);
.Clock{font-family:"Francois One", sans-serif;color:#199b81;font-size:30px;margin:40px}

body{background:black;font-family:"Francois One", sans-serif}.App{text-align:center;color:wheat;display:flex;justify-content:center;align-items:center;flex-direction:column}.App__button{margin:15px;width:400px;height:50px;font-size:15px;font-family:"Francois One", sans-serif;letter-spacing:3px;background:linear-gradient(#199b81, #38aca3);border-radius:4px;border:0;color:white;transition:0.5s}.App__button:hover{color:black;letter-spacing:14px;border-radius:15px;background:linear-gradient(#2bba9d, #50beb5);-webkit-transform:skew(-10deg);transform:skew(-10deg)}

